// Main Styles

*, :after, :before {
	box-sizing: border-box;
	}

// * {
// 	padding: 0;
// 	margin: 0;
// }


div {
	display: block;
}

footer {
	background-color: #323544;
}

footer ul li a,
footer ul li a:hover {
	color: #fff;

}

footer ul {
	list-style: none;
	padding: 0;
	margin: 0;
}

footer h4 {
	color: #fff;
	text-transform: uppercase;
	font-size: 1rem;
	margin-bottom: 26px;
}

ul {
	display: block;
	list-style-type: disc;
	margin-block-start: 1em;
	margin-block-end: 1em;
	margin-inline-start: 0px;
	margin-inline-end: 0px;
	padding-inline-start: 40px;
}

a {
	text-decoration: none;
}

a:hover {
	cursor: pointer;
	text-decoration: underline;
}



// Class Styles 

.container {
	margin-left: auto;
	margin-right: auto;
	padding-left: 15px;
	padding-right: 15px;

}
		
.footer-bottom, .license {
	text-decoration: none;
	list-style: none;
	color: #fff;
	display: flex;
	padding: 5px 0 10px;
	width: 100%;
}

.footer-bottom {
	display: flex;
	justify-content: space-between;
	font-size: .825rem;
	padding-top: 10px;
	width: 100%;	
	background: #212330;
	left: 0;
	border: 0;
}